body {
  background-color: white;
}

#wpcontent {
  padding-left: 0px !important;
}

.wrap {
  padding: 0px;
  margin: 0px;
}

#wpfooter {
  display: none;
}

.sqt-queries-table {
  width: 100%;
  border-collapse: collapse;
}
.sqt-queries-table th, .sqt-queries-table td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}
.sqt-queries-table th {
  font-weight: bold;
}
.sqt-queries-table .sqt-sort-button {
  background: none;
  border: none;
  cursor: pointer;
  font-weight: bold;
  padding: 0;
}
.sqt-queries-table .sqt-sort-button:hover {
  color: #007cba;
}

.sqt-bar-chart {
  position: relative;
}
.sqt-bar-chart .sqt-bar-label {
  position: relative;
  z-index: 1;
  left: 5px;
}
.sqt-bar-chart .sqt-bar {
  background: rgba(255, 154, 0, 0.3);
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 154, 0, 0.05) 30%, rgba(255, 0, 64, 0.3) 100%);
  border-radius: 2px;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}

.sqt-row-clickable {
  cursor: pointer;
}
.sqt-row-clickable:hover {
  background-color: rgba(255, 154, 0, 0.05);
}

.view-links {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
  color: rgb(0, 140, 234);
  margin-left: 10px;
  font-size: 14px;
}

.sqt-clicks-content {
  max-height: 60vh;
  overflow-y: auto;
}

.sqt-clicks-table {
  width: 100%;
  border-collapse: collapse;
}
.sqt-clicks-table th, .sqt-clicks-table td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}
.sqt-clicks-table th {
  font-weight: bold;
  background-color: #f9f9f9;
}

.sqt-no-clicks {
  padding: 15px;
  background-color: #f8f9fa;
  border-left: 4px solid #007cba;
}